*{
  margin: 0;
  padding: 0;
}

body {
  text-align: center;
  color: #222;
  font-size: 24px; 
  font-family: sans-serif;
}

h1 {
  margin: 30px;
}

.languages {
  height: 10%;
  width: 500px;
  margin: 10px auto;
  display: flex;
  flex-direction: row;
}

.language {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 40px;
  background-color: blanchedalmond;
  border: 1px solid #222;
  margin: 2px;
}

.voteCount {
  border-radius: 50%;
  border: 2px solid #222;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px; 
}

button {
  color: green;
  background-color: #0000;
  border: none;
  font-size: 30px;
  outline: none;
  cursor: pointer;
}

.VotingField{
  display: flex;
  background-color: grey;
}

